//===== RESET
@import '~@csstools/normalize.css';

//===== VARIABLES

//----- Fonts
@import url('https://fonts.googleapis.com/css?family=Dancing+Script|Roboto|Rock+Salt|Material+Icons&display=swap');
$font-name: 'Dancing Script', cursive;
$font-logo: 'Rock Salt', cursive;
$font-main: 'Roboto', sans-serif;

//----- Colors

$brand-white: #EFF4F5; //used as background to counter pure white
$pure-white: #FFFFFF;
$brand-black: #000000;
$brand-primary: #00635D;
$brand-secondary: #D62246;
$shadow: 5px 5px 8px -10px rgba(0,0,0,0.75);

//----- Break point(s) -- currently only utilisng one
$width: 700px;


//===== CSS


//===================================
// BASE
//===================================
html, body {
  font-family: $font-main;
  height: 100%;
  margin: 0;
  background-color: $brand-white;
}

//===================================
// LAYOUT :: MOBILE FIRST
//===================================
* {
  box-sizing: border-box; 
}

//--- component wrapper
.container {
  min-height: 100vh;
  position: relative;
}
.content-container {
  margin: 0 1rem 3rem 1rem;
  padding-bottom: 2rem;
  background-color: $pure-white;
  box-shadow: $shadow;
}
.content-container:first-of-type {
  margin-top: 5rem;
}

//--- flex containers
.flex-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
}
.flex-standard {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.flex-horizontal {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.flex-wrap {
  display: flex;
  flex-wrap: wrap;
}
.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid $brand-primary;
  box-shadow: $shadow;
  padding: 0.25rem 1rem 0 0.5rem;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: $brand-white;
  z-index: 1; //always on top
  min-height: 32px;
}
.about {
  .flex-horizontal {
    justify-content: flex-start;
  }
  .flex-container {
    justify-content: space-evenly;
  }
}
//--- related content wrapper
.content, .accordion {
  margin: 0.5rem 1rem;
  padding: 1rem;
}

//===================================
// CARDS
//===================================
.card {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0.5rem;
  background-color: $pure-white;
  margin: 0.1rem;
  box-shadow: $shadow;
}

//===================================
// TEXT
//===================================
.signature {
  font-family: $font-name;
  font-size: 1.5rem;
  text-align: right;
  margin: 1rem 2rem 0 0;
  color: $brand-secondary;
}
a {
  color: $brand-primary;
  text-decoration: none;
}
a:hover {
  color: $brand-secondary;
  text-decoration: underline;
}
h1 {
  margin-block-start: 0.5rem;
  font-size: 1.5em;
}
h2 {
  margin-block-end: 0.5rem; //reduce the bottom margin that is specified in normalize.css
  text-transform: uppercase;
}
ul, p {
  margin-block-start: 0;
  padding-inline-start: 0;
}
li {
  margin: 0 0.5rem;
  list-style-type: none;
}
.home li, .examples li {
  text-transform: uppercase;
}
.tech-stack li {
  text-transform: none;
  margin-bottom: 0.5rem;
}
.contact li {
  margin: 1rem 0;
}
.app-info {
  font-style: italic;
}
.logo p {
  margin: 0;
  padding: 0;
  font-family: $font-logo;
  color: $brand-secondary;
  font-weight: bold;
}
.logo:hover {
  text-decoration: none;
} 


//===================================
// IMAGERY
//===================================
.icons {
  width: 100%;
  text-align: center;
}
// default svg sizing for use inline
svg {
  width: 2rem;
}
.tech-stack svg {
  width: 1.5rem;
  margin-right: 0.5rem;
}
a svg, p svg {
  margin-right: 0.5rem;
}
.icon-link path {
  fill: $brand-secondary;
}
.devicon path {
  fill: $brand-primary;
}
.project-example {
  max-width: 100%;
  box-shadow: 10px 10px 24px -16px rgba(0,0,0,0.75);
}
.creature img {
  width: 60px;
}
.headshot {
  border: 10px solid $brand-black;
  margin: 1rem 0 0 0;
}
.headshot img {
  max-height: 230px;
  border-style: inset;
  border-width: 5px;
  border-color: $brand-secondary;
  margin-bottom: -5px;
}
//arrow inspiration from https://codepen.io/JoshMac/pen/MaYEmJ, with small adjustments
//px coded to ensure it keeps the same shape and position regardless of device
.arrow {
  position: absolute;
  bottom: 75px;
  width: 50px;
  height: 50px;
  transform: rotate(45deg);
  border-left: none;
  border-top: none;
  border-right: 10px $brand-secondary solid;
  border-bottom: 10px $brand-secondary solid;
}
.arrow::before {
  position: absolute;
  content: '';
  width: 20px;
  height: 20px;
  top: 50%;
  margin: -10px 0 0 -10px;
  border-left: none;
  border-top: none;
  border-right: 1px $brand-secondary solid;
  border-bottom: 1px $brand-secondary solid;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-name: arrow;
}
@keyframes arrow {
  0% { 
    opacity: 1; 
  }
  100% {
    opacity: 0;
    transform: translate(-10px, -10px);
  }
}

//===================================
// HOMEPAGE : specific styles only used here
//===================================
.home {
  background-color: $brand-primary;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  h1 {
    // margin: 0 0 2rem 0;
    margin: 0;
    font-family: $font-name;
    color: $brand-white;
    font-size: 5rem;
  }
  ul {
    // margin-bottom: 3rem;
  }
  li {
    margin: 1rem 2rem 0 2rem;
    display: inline-block;
  }
  a {
    color: $brand-black;
    text-decoration: underline;
  }
  a:hover {
    color: $brand-secondary;
  }
  .blurb {
    width: 50%;
    font-size: 0.85rem;
    margin-bottom: 2rem;
  }
  .tagline {
    width: 70%;
    margin-bottom: 2rem;
  }
}
.home.container {
  margin-top: 0;
}

.creature-container li {
  position: relative;
  z-index: 1;
  background-color: $brand-primary;
  overflow-y: hidden;
  padding-top: 16px;

.creature {
  display: block;
  background-size: cover;
  width: 32px;
  height: 32px;
  position: absolute;
  animation: move 5s ease;
}
.one {
  background-image: url('https://github.com/jennikate/portfolio/blob/master/src/styles/images/jp-elf.png?raw=true');
  animation-delay: 1s;
  // animation-iteration-count: 2;
}
.two {
  background-image: url('https://github.com/jennikate/portfolio/blob/master/src/styles/images/jp-tief.png?raw=true');
  animation-delay: 3s;
}
.three {
  background-image: url('https://github.com/jennikate/portfolio/blob/master/src/styles/images/jp-mage.png?raw=true');
  animation-delay: 6s;
}
.four {
  background-image: url('https://github.com/jennikate/portfolio/blob/master/src/styles/images/BARD.png?raw=true');
  animation-delay: 9s;
  left: 30px;
}
.five {
  background-image: url('https://github.com/jennikate/portfolio/blob/master/src/styles/images/gnome.png?raw=true');
  animation-delay: 12s;
  left: 100px;
  }
}
@keyframes move {
  0% { bottom: -35px; }
  50% { bottom: 0px; }
  100% { bottom: -35px; }
}


//===================================
// NAV : specific styles only used here
//===================================
nav .creature img {
  // width: 32px;
  width: 25px;
}

//===================================
// CONTACT : specific styles only used here
//===================================
.contact {
  .content-container {
  background-color: $brand-white;
  box-shadow: none;
  }
}



//===================================
// MENU : specific styles only used here
//===================================
.menu-box {
  margin-right: 0.5rem;
}
.creature.open img {
  transform: scaleX(-1);
}
.open + a {
  text-transform: uppercase;
}
.menu-box:hover {
  .creature, .line {
    transform: scaleX(-1);
    // animation-name: bounce;
    // animation-duration: 1s;
    // animation-timing-function: ease;
    // animation-iteration-count: 1;
  }
  .open {
    transform: none;
  }
}
.open + a:hover {
  color: $brand-primary;
  text-decoration: none;
  cursor: default;
}


//===================================
// BREAKPOINT ADJUSTMENTS
//===================================

// at roughly tablet landscape the layout moves to two columns
@media only screen and (min-width: $width) {

//--- containers
  .container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .flex-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }
  .flex-standard {
    align-items: flex-start;
  }
  .about {
    .content {
      width: 60%; 
    }
  }
  .examples {
    .container.accordion {
      align-items: flex-start;
    }
    .accordion-label.flex-horizontal {
      justify-content: flex-start;
    }
    .content {
      width: 40%;
    }
    .content:first-of-type {
    max-width: 20%
    }
    .content:nth-of-type(2n) {
      height: 100%;
    }
  }
  .contact {
    .headshot {
      margin: 0 5rem 0 0;
    }
    .flex-container {
      align-items: flex-end;
    }
    .flex-horizontal {
      justify-content: flex-start;
    }
    ul {
      margin-bottom: 0;
    }
  }
  .menu {
    .menu-box {
      margin-right: 1rem;
    }
  }
}